import { useFly } from "@/hooks/fly";
import { parseHTML } from "@/utils/html";
import {
  GetBookChapterList,
  GetBookClassifyList,
  GetBookContent,
  GetBookDetail,
  GetBookList,
  SearchBook,
  SearchBookResult
} from "../type";
import { Book } from "@/interfaces/book";
import { Chapter } from "@/interfaces/chapter";
import { Classify } from "@/interfaces/classify";

const bookOrigin = "lengku8"; // 

/**
 * @description: 
 */
export const getBookList: GetBookList = async (params) => {
  const res = await useFly().get(`https://www3.lengku8.cc/category/${params.classifyId}/${params.current}.html`);
  const $ = parseHTML(res.data);

  const listPromise = $(".CGsectionTwo-right-content")
    .children()
    .map<Promise<Book>>((_i, el) => {
      const id =
        $(el)
          .find(".title")
          .attr("href")
          ?.match(/\/book\/(\d+)\//)?.[1] || "";
      if (params.noImageMode) {
        const title = $(el).find(".title").text();
        const author = $(el).find("a.b").text();
        const authorId = $(el)
          .find("a.b")
          .attr("href")!
          .match(/\/writer\/(\d+)\//)![1]; // id
        const intro = $(el).children("p").eq(2).text();
        const updateDate =
          $(el)
            .children("p")
            .eq(3)
            .text()
            .match(/[\d-]+/)?.[0] || "";
        return Promise.resolve({
          id,
          title,
          cover: "",
          intro,
          author: {
            id: authorId,
            name: author
          },
          classify: {
            id: "",
            name: ""
          },
          updateDate,
          lastChapter: {
            id: "",
            title: "",
            isLock: false
          },
          state: -1,
          bookOrigin
        });
      } else {
        return getBookDetail({ id });
      }
    })
    .toArray();
  const list = await Promise.all(listPromise);
  const current = Number($(".CGsectionTwo-right-bottom-detail span").eq(0).text() || 0);
  const size = Number($(".CGsectionTwo-right-bottom-detail span").eq(2).text() || 0);
  const total = Number($(".CGsectionTwo-right-bottom-detail span").eq(1).text() || 0) * size;

  return { current, size, total, list };
};

/**
 * @description: 
 */
export const getBookDetail: GetBookDetail = async (params) => {
  const res = await useFly().get(`https://www3.lengku8.cc/book/${params.id}/`);
  const $ = parseHTML(res.data);

  const title = $(".BGsectionOne-top-right .title").eq(0).text(); // 
  const author = $(".BGsectionOne-top-right .author .b").eq(0).text(); // 
  const authorId = $(".BGsectionOne-top-right .author .b")
    .eq(0)
    .attr("href")!
    .match(/\/writer\/(\d+)\//)![1]; // id
  const categorys = $(".BGsectionOne-top-right .category")
    .eq(0)
    .children("span")
    .map((i, el) => $(el).text())
    .toArray(); // 
  const classifyId =
    $(".BGsectionOne-top-right .category").eq(0).find("span a").eq(0).attr("href")?.match(/\d+/)?.[0] || ""; // id
  const updateDate = $(".BGsectionOne-top-right .time").eq(0).children("span").text(); // 
  const cover = $(".BGsectionOne-top-left .lazyload").attr("_src") || ""; // 
  const intro = $(".BGsectionTwo-bottom").text(); // 
  const lastChapter = $(".BGsectionOne-top-right .newestChapter .r").text(); // 
  const lastChapterId =
    $(".BGsectionOne-top-right .newestChapter .r")
      .attr("href")
      ?.match(/(\d+)\.html/)?.[1] || ""; // id

  return {
    id: params.id,
    title,
    cover,
    intro,
    author: {
      id: authorId,
      name: author
    },
    classify: {
      id: classifyId,
      name: categorys[0]
    },
    updateDate,
    lastChapter: {
      id: lastChapterId,
      title: lastChapter,
      isLock: false
    },
    state: ["", ""].indexOf(categorys[2]) as Book["state"],
    bookOrigin
  };
};

/**
 * @description: 
 */
export const getBookChapterList: GetBookChapterList = async (params) => {
  const list: Chapter[] = [];
  let currentPage = 1;
  let totalPage = 0;
  do {
    const res = await useFly().get(`https://www3.lengku8.cc/book/${params.id}/catalog/${currentPage}.html`);
    const $ = parseHTML(res.data);

    const originalOrderArrStr = res.data.match(/var originalOrder = \[(.+)\];/)?.[1]; // 
    const tempList: Chapter[] = [];

    if (originalOrderArrStr) {
      const originalOrder = originalOrderArrStr.split(","); // 
      $(".BCsectionTwo-top .BCsectionTwo-top-chapter a").each((i, el) => {
        tempList[originalOrder[i]] = {
          id: $(el)
            .attr("href")!
            .match(/(\d+)\.html/)![1],
          title: $(el).attr("data-real")!,
          isLock: false
        };
      });
    } else {
      $(".BCsectionTwo-top .BCsectionTwo-top-chapter").each((i, el) => {
        const pos = $(el).attr("data-x")!;
        const a = $(el).children("a");
        if (a.attr("data-link")) {
          tempList[pos] = {
            id: atob(a.attr("data-link")!).match(/(\d+)\.html/)![1],
            title: a.attr("abc-title")!
          };
        } else {
          tempList[pos] = {
            id: atob(a.attr("data-sb")!).match(/(\d+)\.html/)![1],
            title: a.attr("uc-title")!
          };
        }
      });
    }

    list.push(...tempList);
    totalPage = Number($(".CGsectionTwo-right-bottom-detail span").eq(1).text() || 0);
    currentPage++;
  } while (currentPage <= totalPage);

  return list;
};

/**
 * @description: 
 */
export const getBookContent: GetBookContent = async (params) => {
  const res = await useFly().get(`https://www3.lengku8.cc/book/${params.id}/${params.chapterId}.html`);
  const $ = parseHTML(res.data);

  const paragraphs: string[] = [];
  let isEnd = false;
  $(".RBGsectionThree-content p").each((i, el) => {
    if ($(el).attr("style") === "color:orange;") isEnd = true;
    if (isEnd) return;
    paragraphs.push($(el).text());
  });
  const title = $("#chapterTitle").text(); // 
  const prevChapterId = ($(".RBGsectionTwo-left .qian_page")
    .attr("href")!
    .match(/(\d+)\.html/) || [])![1]; // id
  const nextChapterId = ($(".RBGsectionTwo-right .hou_page")
    .eq(0)
    .attr("href")!
    .match(/(\d+)\.html/) || [])![1]; // id

  return { chapterId: params.chapterId, title, paragraphs, prevChapterId, nextChapterId };
};

/**
 * @description: 
 */
export const getBookClassifyList: GetBookClassifyList = async () => {
  const res = await useFly().get(`https://www3.lengku8.cc/category/`);
  const $ = parseHTML(res.data);

  const list: Classify[] = [];
  $(".CGsectionTwo-left a").each((i, el) => {
    if (!$(el).text()) return;
    list.push({
      id: $(el).attr("href")!.match(/\d+/)![0],
      name: $(el).text()
    });
  });

  return list;
};

/**
 * @description: 
 */
export const searchBook: SearchBook = async (params) => {
  const res = await useFly().get(`https://www3.lengku8.cc/search/${params.searchValue}/${params.current}`);
  const $ = parseHTML(res.data);

  const list: SearchBookResult["list"] = [];
  $(".SHsectionThree-middle p").each((i, el) => {
    const span = $(el).children("span");
    list.push({
      id: span.eq(1).children("a").attr("href")?.match(/\d+/)?.[0] || "",
      classify: {
        id: span.eq(0).children("a").attr("href")?.match(/\d+/)?.[0] || "",
        name: span.eq(0).children("a").text()
      },
      title: span.eq(1).children("a").text(),
      author: {
        id: span.eq(2).children("a").attr("href")?.match(/\d+/)?.[0] || "",
        name: span.eq(2).children("a").text()
      },
      bookOrigin
    });
  });
  const current = Number($(".CGsectionTwo-right-bottom-detail span").eq(0).text() || 0);
  const size = list.length;
  const total = Number($(".CGsectionTwo-right-bottom-detail span").eq(1).text() || 0) * size;

  return { list, current, size, total };
};
